@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400;500;600;700&family=Montserrat:wght@400;500&display=swap');
.gallery {
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.titre-gallery {
    text-align: center;
    font-family: "Cormorant";
    color:purple;
    font-weight: 700;
    font-size: 25px;
    line-height: 20px;
    padding-top: 40px;
}
.galleryCard {
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    padding-top:30px;
    padding-left:2%;
    padding-right:2%;
    border-radius: 5px;
    width: 60%;
    height:450px;
}
@media  screen and (max-width:768px) {
    .galleryCard {
        width:96%;
        flex-direction: column;
        flex-wrap: nowrap;
        height:auto;
    }
}
